
    @font-face {
        font-family: Poppins;
        src: url(../assets/fonts/Poppins-Regular.ttf);
    }


*{
    box-sizing: border-box;
  }

body{
    background-color: royalblue;
    font-family: Poppins;
}

.App {
    text-align: center;  
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;

  }
  
  .App-link {
    color: #61dafb;
  }
  
  button{
    color: white;
    background: radial-gradient(circle, rgba(9,19,121,1) 0%, rgba(21,0,255,1) 0%, rgba(9,19,121,0.9220063025210083) 100%);
    // border: 1px white solid ;
    padding: 5px 10px;
    border-radius: 3rem;
    &:hover{
        cursor: pointer;
        background: #61dafb;
    }
  }

  .contact-page{
      h1{
          color: white;
      }
      .lists{
          display: flex;
          justify-content: space-around;
          .list-container{
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;
            justify-content: center;
            padding: 10px;
            gap: 15px;
            .contact-preview{
                background-color: lightsteelblue;
                -webkit-box-shadow: 0px 0px 16px 3px rgba(255,255,255,0.35); 
                box-shadow: 0px 0px 16px 3px rgba(255,255,255,0.35);
                display: flex;
                // flex-direction: column;
                justify-content: flex-end;
                align-items: center;
                gap: 10px;
                // min-width: 100%;
                padding: 10px 50px;
                &>*{
                    margin-inline-start: 10px;
                }
                    img{
                        object-fit: contain;
                        max-height: 50px;
                    }
              }
          }
          .moves-list{
            background-color: lightsteelblue;
            height: fit-content;
            ul{
                list-style-type: none;
                padding: 0;
                li{
                    border: 1px black solid;
                    padding: 5px;
                    
                }
            }
          }
      }
  }

  @media screen and (max-width: 700px) {
    .contact-page{
        .lists{
            flex-direction: column;
            align-items: center;
            .list-container{            
                .contact-preview{
                    padding: 0px 1px;
                }
            }
        }
    }
  }

  .statistic-page{
    color: white;
  }
  .home-page{
      color: white;
  }
  .contact-details-page{
    color: white;
    a{
        color: white;
    }
    .errorMsg{
        color: red;
    }
  }

  .contact-filter{
    margin-bottom: 50px;
    color: white;
    // background: radial-gradient(circle, rgba(9,19,121,1) 0%, rgba(21,0,255,1) 0%, rgba(9,19,121,0.9220063025210083) 100%);
    a{
        color: white;
    }
  }
  .chart{
    max-width: 500px;
    margin: auto;
  }
  header{
    display: flex;
    justify-content: space-around;
    color: white;
    background: radial-gradient(circle, rgba(9,19,121,1) 0%, rgba(21,0,255,1) 0%, rgba(9,19,121,0.9220063025210083) 100%);
    padding: 15px;
    a{
        color: white;
        text-decoration: none;
    }
  }
  a:hover{
    cursor: pointer;
    transform:scale(1.1);
    text-decoration: underline;
  }
  .contact-edit-page form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 6px;
  }
  .contact-edit-page .actions{
    display: flex;
    justify-content: space-around;
  }
  .navigation{
    display: flex;
    justify-content: space-around;
  }
  
  .search-bar{
    padding: 5px;
  }
  .search-bar>*{
    margin-inline-end: 10px;
  }
  .signup-page form{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
  }